'use client';

import Link from 'next/link';
import { useEffect, useState } from 'react';
import { Menu, X } from 'lucide-react';
import HamburgerMenu from './hamburger';
import { usePathname } from 'next/navigation';
import { useTheme } from 'next-themes';

export default function Header() {
  const [isOpen, setIsOpen] = useState(false);
  const [scrolled, setScrolled] = useState(false);

  const pathname = usePathname();
  const { theme, setTheme } = useTheme();

  const navItems = [
    { href: '/', label: 'Inicio' },
    { href: '/amortization', label: 'Amortización' },
    { href: '/bonification', label: 'Bonificación' },
  ];

  useEffect(() => {
    const onScroll = () => {
      setScrolled(window.scrollY > 0);
    };
    window.addEventListener('scroll', onScroll, { passive: true });
    return () => window.removeEventListener('scroll', onScroll);
  }, []);

  return (
    <header className={`sticky top-0 z-20 mx-auto px-4 md:px-12 flex w-full max-w-6xl items-center justify-between gap-4 py-3 transition-all ${scrolled ? 'bg-background-header backdrop-blur-xs' : ''}`}>
      <div className="flex gap-12">
        {/* Logo */}
        <Link href="/" className="flex items-center justify-start gap-2 text-xl font-bold">
          <img alt="Logo" width="40" height="40" decoding="async" data-nimg="1" src="/logo1.png" />
          <span className="hidden md:block">PerFi</span>
        </Link>

        {/* Menú desktop */}
        <nav className="hidden sm:flex items-center justify-start gap-2 ">
          {navItems.map(({ href, label }) => (
            <Link
              key={href}
              href={href}
              className={`flex py-2 px-2 font-semibold transition-colors
                ${pathname === href
                  ? 'text-primary dark:text-primary-dark'
                  : 'text-text-light dark:text-muted-dark hover:text-primary dark:hover:text-primary-dark'}
              `}
            >
              {label}
            </Link>
          ))}
        </nav>
      </div>
      <div className="flex gap-2 items-center">
        {/* Theme toggle usando next-themes */}
        <button
          aria-label="Cambiar tema"
          className="mr-4 p-2 rounded transition-colors hover:bg-background-light dark:hover:bg-background-dark"
          onClick={() => setTheme(theme === 'dark' ? 'light' : 'dark')}
        >
          {theme === 'dark' ? (
            <svg className="w-5 h-5" fill="none" stroke="currentColor" strokeWidth={2} viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" d="M12 3v1m0 16v1m8.66-13.66l-.71.71M4.05 19.07l-.71.71M21 12h-1M4 12H3m16.66 5.66l-.71-.71M4.05 4.93l-.71-.71M16 12a4 4 0 11-8 0 4 4 0 018 0z" />
            </svg>
          ) : (
            <svg className="w-5 h-5" fill="none" stroke="currentColor" strokeWidth={2} viewBox="0 0 24 24">
              <path strokeLinecap="round" strokeLinejoin="round" d="M21 12.79A9 9 0 1111.21 3a7 7 0 109.79 9.79z" />
            </svg>
          )}
        </button>
        <HamburgerMenu />
      </div>

      {/* Menú móvil desplegable */}
      {isOpen && (
        <nav className="fixed inset-0 z-40 flex flex-col p-6 pt-20 space-y-6 xl:hidden">
          {navItems.map(({ href, label }) => (
            <Link
              key={href}
              href={href}
              onClick={() => setIsOpen(false)}
              className="text-xl font-semibold hover:text-primary transition-colors"
            >
              {label}
            </Link>
          ))}
        </nav>
      )}
    </header>
  );
}
